export default {
	props: {
		chatList: {
			type: Array,
			default: [{
				avatar: 'https://ossweb-img.qq.com/images/lol/img/champion/Taric.png',
				badgeNum: 1,
				time: '10:30',
				name: '昨日少年郎',
				desc: '正在抓紧开发'
			},
			{
				avatar: 'https://ossweb-img.qq.com/images/lol/img/champion/Taric.png',
				badgeNum: 1,
				time: '10:30',
				name: '昨日少年郎',
				desc: '正在抓紧开发'
			},
			{
				avatar: 'https://ossweb-img.qq.com/images/lol/img/champion/Taric.png',
				badgeNum: 0,
				time: '10:30',
				name: '昨日少年郎',
				desc: '正在抓紧开发'
			},
			{
				avatar: 'https://ossweb-img.qq.com/images/lol/img/champion/Taric.png',
				badgeNum: 1,
				time: '10:30',
				name: '昨日少年郎',
				desc: '正在抓紧开发'
			},
			{
				avatar: 'https://ossweb-img.qq.com/images/lol/img/champion/Taric.png',
				badgeNum: 1,
				time: '10:30',
				name: '昨日少年郎',
				desc: '正在抓紧开发'
			}]
		}
	},
	data() {
		return {
			listTouchStart: 0,
			listTouchDirection: null,
			modalName: null
		}
	},
	methods: {
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},
		
		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},
		
		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		}
	}
}
